<template>
<v-container class="flex-col h-100 w-100 projects" fluid>
    <v-row class="flex-grow-0">
        <v-col class="pa-0">
                       <!--    -->
                <filters-bar 
                @sendData="updateData"
                @clear="clear"
                class="pa-1 pb-3"
                  
                    :header="pagetitle"
                >
                  <template v-slot:filterslist>
                      <f-input-search
                        
                        :field="'name'"
                        title=""
                    ></f-input-search>
                      <f-daterange
                    
                        :field="'datestart__gte'"
                        title="  :"
                        
                    ></f-daterange>
                        <f-daterange
                       
                        :field="'datestart__lte'"
                        title="  :"
                        
                    ></f-daterange>
                    <f-daterange
                  
                        :field="'dateend__gte'"
                        title="  c:"
                        
                    ></f-daterange>
                        <f-daterange
                     
                        :field="'dateend__lte'"
                        title="  :"
                        
                    ></f-daterange>
                      
                  
                     <f-multi-select
                        
                            :title="' '"
                            :field="'status'"
                            :disabled="false"
                        ></f-multi-select>
                    <f-input-search
                        
                        :field="'user_project__user__sn'"
                        title=" "
                    ></f-input-search>



                       
                             <f-multi-select
                         
                            :title="''"
                            :field="'user_project__role'"
                            :disabled="false"
                        ></f-multi-select>
                        
                  </template>
                </filters-bar>
                       
        </v-col>

    </v-row>


    
    <v-row class="mt-0 ">
        <v-col class="flex-col pa-1">
              <v-card class="flex-grow-1">
           
        <div class="mt-3 iconInline">
        <BtnsOut      
            v-if="pagetitle==''"        
            :widthi=140
            :icon = true
            title = " "
            @click="createproj"
            color="open_icons"
      >
                       
      </BtnsOut>

             
                </div>

    <v-data-table
        :fixed-header="true"
        @dblclick:row="clickRow"
        :disable-pagination="true"
        hide-default-footer
        :headers="!into_project?headers:childHeaders"
        :items="!into_project?parentList:childList"
        class="grow-table"
    >
     <template v-slot:[`item.link`]="{ item }">
 
        
                <v-icon v-if="!into_project && item.all>0" @click.stop="toChild(item)">mdi-chevron-right</v-icon>
                <v-icon v-else-if="into_project" @click.stop="backParent">mdi-chevron-left</v-icon>
            </template>

             <template v-slot:[`item.user_project`]="{ item }">
                {{find_rp(item.user_project)}}
            </template>
            

             <template v-if="into_project" v-slot:[`item.id`]="{ item }">
                {{projectid}}-{{item.id}}
            </template>
          
            <template v-slot:[`item.dateend`]="{ item }">
                {{timediff(item.dateend) }}
            </template>
            <template v-slot:[`item.lastchanged`]="{ item }">
                {{formatDate(item.lastchanged) }}
            </template>
            <template v-slot:[`item.bars`]="{ item }">
                <div class="d-flex align-center">
                  <template v-if="'completed' in item && item.all>0">
                    <v-progress-linear  style="width: 80%;" :value="item.completed/item.all*100" height="10" buffer-value="100"></v-progress-linear>
                    <div class="ps-2">{{`${item.completed}/${item.all}`}}</div>
                  </template>    
                <div class="mx-auto" v-else><v-icon v-if="item.status__id==5" style="color:green">fa-solid fa-square-check</v-icon></div>





                    <v-menu offset-x left v-if="superuser">
                        <template v-slot:activator="{ on, attrs }">
                           
                            <div v-bind="attrs"
                                v-on="on" 
                                class="ms-2 dots-vertical">
                                <v-icon>mdi-dots-vertical</v-icon>
                            </div>
                        </template>
                        <v-list>
                            <v-list-item><v-list-item-title></v-list-item-title></v-list-item>
                        </v-list>
                    </v-menu>
                    <div v-else style="width: 32px;">

                    </div>
                    <div @click.stop="menu" class="ps-2 dots-vertical"></div>
                </div>
            </template>
    
    
    
    
    </v-data-table>
       

        </v-card>

        </v-col>



    </v-row>


 <div class="text-center">
    <v-dialog
      v-model="dialog_create_project"
      width="75vw"
      
    >
 <!-- <all-info-project></all-info-project> -->
     <project-stepper-pattern
      @newproject='newproject'
      @showuserinfo='showuserinfo'
      @closes="dialog_create_project=!dialog_create_project"
     />


    </v-dialog>

          <infousercard
        :dialogdetail="dialogdetail"
        :person="person"
        @dialogdetailoff="dialogdetail=false"
    />
  </div>





</v-container>

</template>
<script>
import moment from "moment"
import { createNamespacedHelpers } from "vuex";
import FiltersBar from '../components/UI/FiltersBar.vue';
import FInputSearch from '../components/UI/FInputSearch.vue';
import {axiosInstance} from '@/utils/api'
import { required } from "vuelidate/lib/validators";
import { validationMixin } from "vuelidate";
import rowPirow from '@/components/carddetails/pattern/row-pirow.vue';
import RolesSingle from '@/components/assignments/UI/roles-single.vue';
import rolesMany from '@/components/assignments/UI/roles-many.vue';
import RowsAddremove from '@/components/carddetails/pattern/rows-addremove.vue';
import ProjectStepperPattern from  '@/components/UI/projectsteppersteps/projectStepperPattern.vue';
import infousercard from '@/components/UI/infousercard.vue';
const { mapMutations, mapActions } = createNamespacedHelpers("filterslogic");
const globalstore = createNamespacedHelpers("globalstore");
const projectstate = createNamespacedHelpers("projectstate");
import uf from '@/utils/universal_functions'

export default {

    components: {
      FiltersBar,
      FInputSearch,
      rowPirow,
      RolesSingle,
      rolesMany,
      RowsAddremove,
      infousercard,
      ProjectStepperPattern,

    },

    mixins: [validationMixin, uf],

    data: () => ({

        nameproj:'',
         
        dialog_create_project: false,
        model:'',
        search: '',       
        ws: null,
        projectid:null,
        superuser: false,  
        childHeaders:[
            {text: " ", value: "link"},
            {text:"",value:"id"},

            {text:"", value: "name"},
            {text:"", value:"responsible.sn"},
            {text:"", value: "dateend"},
            {text:"", value: "status.name"},

        ],      
        headers: [
            {text: " ", value: "link"},
            {text:"",value:"id"},

            {text:"", value: "name"},
            {text:"", value:"user_project"},
            {text:"", value: "dateend"},
            {text:"", value: "status.name"},
            {text:"", value: "lastchanged"},
            {text:"", value: "bars"},
        ],
        into_project: false,
        pagetitle:'',      
        menu: false,
        childList:[],

        parentList:[],
        person:{} ,

        dialogdetail: false
        
      
       
}),

    

    methods: {   
        ...globalstore.mapMutations(['set_assign_id']),
        ...mapMutations(["setFields"]),
        ...mapActions(["clear", "getFilters", "addFilter", "activefilters"]),
        ...globalstore.mapGetters(["getassign_id"]),
        ...projectstate.mapMutations(["setProject","setProjectusers"]),

        createproj(){
            console.log('-----------dialog_create_project');
            this.dialog_create_project = true
            
            // console.log(res, 'res');
            // this.$router.push(`/project/${res.data.id}`)
           

                   
                    //  console.log(`plok`, val);
                            },

        showuserinfo(val){

            this.person = this.searchUser(val, this.getProject.users )
            this.dialogdetail=true
        },

        async newproject(val) {
         
         
        // console.log(val)
        let res = await axiosInstance.post('/createproj',val)
        this.dialog_create_project=false
        this.$router.push(`/project/${res.data.id}`) 
        
        },


        timediff(dateend) {
            return moment(dateend).isValid()? moment(dateend).locale('ru').fromNow(): ' '
        },
        formatDate(date) {
            return (date && moment(date).isValid())? moment(date).format("DD.MM.YYYY"): '' 
        },

        
        
        build(data) {
            
            if (!this.into_project){
            if (data.payload.length>0){    
            data.payload.forEach(el => {

                el["completed"]= el.project_task.filter(el=>el.status.id==5).length
                el["all"]=el.project_task.length
                
            });    
            this.parentList= data.payload
            }
            else{
                this.parentList=[]
            }
            
            }
            else{
                this.childList = data.child
            }
           
            
        },
        async updateData(){
            let payload=await this.activefilters()
            for (let key of Object.keys(payload))
         {
             if ( ['status__in','user_project__role__in'].includes(key))  {
             let arr=[]             
             payload[key].forEach(el => {arr.push(el.id)
                
             });
             payload[key]=arr
             }

         }
            this.ws.send({"action": "getfilter", payload})
        //    console.log(await this.activefilters());
        },
      
        find_rp(arr){            
            const rp = arr.find(el=>el.role.id==2)
            return rp==undefined?" ": rp.user.sn
        },



        update(){
            console.log(`UPDATE`)

                },
        titler(itname=""){            
            if(this.$route.path=="/projects")             {
             this.pagetitle= !this.into_project?"":`${itname}: `
            }else if(this.$route.path=="/tasks"){
             this.pagetitle= !this.into_project?"":`${itname}: `
            }
            else if(this.$route.path=="/works"){
             this.pagetitle= !this.into_project?"":`${itname}: `
            
            }else{

                this.pagetitle=""
            }
        },
      
        toChild(item) {
            this.projectid = item.id
            this.into_project = true;
            
            this.titler(item.name)    
            this.childList=item.project_task
            // this.ws.conn.send(JSON.stringify({"action": "getchild","parent__type":this.$route.path.slice(1), "parent__id": item.id}))
        },

        backParent() {
            this.into_project = false;
            this.titler()
         
        },
        clickRow(e, row)  {
            let path = this.$route.path
            let pusher={
                '/projects':[{name:"Task",'col':'id'},{name:"Project",'col':'id'} ],
                '/tasks':[{name:"Work",'col':'id'},{name:"Task",'col':'id'}],
                 '/works':[{name:"Work",'col':'parent'},{name:"Work",'col':'id'}],
                '/assignments':[{name:"Work",'col':'parent'},{name:"Work",'col':'parent'}]
            }           
            
            let ind = this.into_project? 0:1       
            // console.log("ssss",{ name: pusher[path][ind].name, params: { tid:row.item[pusher[path][ind].col]}}); 
            console.log(" -----",row.item )
            let params = { id:row.item[pusher[path][ind].col]}
            if ((path=='/assignments' && ind==1) || (ind==0 && path=='/works')){
                
                 this.set_assign_id(row.item.id)
                //  console.log(this.getassign_id());
                 
                  this.$router.push({path:`/project/${row.item.project}/task/${row.item.task}/work/${row.item.work}`})
            }
            else if((ind==1 && path=='/works')||(ind==0 && path=='/tasks')){

                this.$router.push({path:`/project/${row.item.project}/task/${row.item.task}/work/${row.item.id}`})
            }

            else if((ind==1 && path=='/tasks')||(ind==0 && path=='/projects')){
                this.$router.push({path:`/project/${this.projectid}/task/${row.item.id}`})
            }
            else{
                    this.$router.push({path:`/project/${row.item.id}`})
            }
               
            // this.$router.push({ name: pusher[path][ind].name, params: params})
            
        },
        wsconnect(){
            this.ws = new this.$pws()               
            this.ws.start(`/reestr${this.$route.path}`);
            this.ws.addEvent('onmessage', (event) => {
            const data = JSON.parse(event.data); 
            console.log(data, "dataonmess");
            try{                  
            this[data.action](data);
            }catch(e){
                console.log(e)
            }
        }
        )
        }
    },
     watch:{
    $route(to, from){
    this.ws.stop()
    this.into_project=false
    this.childList=[]
    this.parentList=[]
    this.titler()
    this.wsconnect()
    
  }
},

  
  
   mounted() {
           
        console.log(`this.newproject`, this.newproject);

        this.superuser = this.$store.getters['auth/getSuperUser']
        this.setFields({ 
           project__status: { module: "mult" },
           project__type: { module: "mult" },
           role:{module:"mult"}
          
        });
        
        this.getFilters([
            {role:{name:"role",sort:"id"}},
            {project__type:{sort: "id", filters:{},exclude:["projects"]}},
            {project__status:{name:"project__status", sort:"id", related_name: "name"}}], true).then(async (data) => {console.log(data,"data")})
        
        this.wsconnect()
        this.titler()
        console.log(this.getProject);
      
    },
    destroyed() {
        this.ws.stop()
    }
}
</script>
<style lang="scss" scoped>
    @import "@/utils/colors.scss";
    
    .main-projects {
        color: $color-main;
    }

    

</style>
<style lang="scss">
    @import "@/utils/colors.scss";
    
        h4 {
            color: $bg-purple;
  
        }
    
    .task-table tbody tr:first-child td {

        color: $color-main;
    }

    tbody {
        cursor: pointer;
    }

    .main-projects .theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr > th {
        font-weight: 600;
    
        color: $color-main;
    }

    .main-projects .theme--light.v-data-table > .v-data-table__wrapper > table tr .dots-vertical i {
        opacity: 0;
    }

    .main-projects .theme--light.v-data-table > .v-data-table__wrapper > table tr:hover .dots-vertical i {
        opacity: 1;
    }

    .grow-table {
        display: flex;
        flex-direction: column;
        overflow-y: auto;
            }

    .iconInline {
        position: absolute;
        z-index: 3;
        left: 1%;
        top: 93%
    }
    .card-text {
      font-size: 14px!important;
      font-weight:400!important
  }
    .long div:nth-child(1) {
      align-self: center;
    }

    .cardwight {
       width: 81vw!important;
    }
  // tr:first-child
  // :nth-child(1)


</style>